class LazyLoad {
    constructor(imgOps, obsOps) {
        this.element = [];
        this.imgOptions = Object.assign(this.defaulImgOps, imgOps|| {});
        this.observeOptions =  Object.assign(this.defaultObserveOptions, obsOps || {});
        this.observer = null;
        // åå§åè§å¯å¨å®ä¾
        this.initObserver();
    }

    /**
     * éæåå§åæ¹æ³
     * @param {*} imgOps å¾çéé¡¹
     * @param {*} obsOps è§å¯å¨éé¡¹
     * @returns LazyLoadå®ä¾
     */
    static init (imgOps, obsOps) {
      return new LazyLoad(imgOps, obsOps);
    }

    /**
     * é»è®¤çè§å¯éé¡¹
     * @returns é»è®¤çè§å¯éé¡¹
     */
   get defaultObserveOptions() {
        return {
            threshold: 0.1, // åç´ è¿å¥è§å£çæ¯ä¾10%
            root: null, // æ ¹åç´ ï¼é»è®¤æ¯è§å£
            rootMargin: '10px', // æ ¹åç´ çå¤è¾¹è·
        }
    }

    /**
     * é»è®¤çå¾çéé¡¹
     * @returns é»è®¤çå¾çéé¡¹
     */
    get defaulImgOps() {
        return {
            // å¤±è´¥å ä½å¾
            error: '', // å¤±è´¥å¾çå°å,
            loading: '', // å è½½å ä½å¾
            // æå è½½å¾ççå±æ§
            attr: 'data-src',
            attrImg: 'src'
        };
    }

    /**
     * åå§åè§å¯å¨å®ä¾
     */
    initObserver() {
        if (!this.observer) {
            this.observer = new IntersectionObserver(this.observeHandler.bind(this), this.observeOptions);
        }
    }

    /**
     * åå§ååç´ èç¹
     * @param {*} el åç´ èç¹æéæ©å¨
     * @returns åç´ èç¹æ°ç»
     */
    elementInit(el) {
        if (!el) {
            throw new Error('elåç´ èç¹åæ°ä¸è½ä¸ºç©º');
        }
        
        let elements = [];
        
        if (typeof el === 'string') {
            // å¦ææ¯éæ©å¨ï¼æ¥è¯¢åç´ 
            elements = document.querySelectorAll(el);
        } else if (el.nodeType === 1) {
            // å¦ææ¯åä¸ªDOMåç´ 
            elements = [el];
        } else if (el instanceof NodeList || el instanceof HTMLCollection) {
            // å¦ææ¯NodeListæHTMLCollection
            elements = Array.from(el);
        } else if (Array.isArray(el)) {
            // å¦ææ¯æ°ç»
            elements = el;
        }
        
        return elements;
    }

    /**
     * å®è£æä»¶
     * @param {*} app vueå®ä¾
     * @param {*} options æä»¶éé¡¹
     */
    install(app, options) {
       const _this = this;
       options = options || {};
       // é»è®¤æä»¤åç§°lazy
       const directiveName = options.name || 'lazy';
        app.directive(directiveName, {
            bind(el, binding) {
                // å°å½åå¾çurlç»å®å°data-srcå±æ§ï¼æ¹ä¾¿åé¢å è½½å°æä»¤ä¸­
                el.setAttribute('data-src', binding.value);
                // è®¾ç½®å è½½å ä½å¾
                if (_this.imgOptions.loading) {
                    el.src = _this.imgOptions.loading;
                }
            },
            inserted(el, binding) {
                // ä½¿ç¨ Intersection Observer çå¬åç´ 
                _this.observe(el);
            },
            componentUpdated(el, binding) {
                // å½å¾çå°åæ¹åæ¶ï¼éæ°è®¾ç½®data-srcå±æ§
                el.setAttribute('data-src', binding.value);
                // éæ°å¼å§è§å¯
                _this.observe(el);
            },
            unbind(el) {
                // ç§»é¤ Intersection Observer çå¬
                _this.unobserve(el);
            },
        })
    }

    // æå¨è°ç¨useæ§è¡çå¬æå®åç´ , ç¡®ä¿data-srcå±æ§å­å¨,ä¸ç¶å è½½å¾çä¼å¤±è´¥
    /**
     * æå¨è°ç¨ä½¿ç¨æä»¶
     * @param {*} el åç´ èç¹æéæ©å¨
     */
    use(el) {
        this.element = this.elementInit(el);
        // å¯¹æ¯ä¸ªåç´ é½çå¬
        this.element.forEach(el => {
            // è®¾ç½®å è½½å ä½å¾
            if (this.imgOptions.loading) {
                el.src = this.imgOptions.loading;
            }
            this.observe(el);
        })
    }

    /**
     * è§å¯åç´ è¿å¥è§å£çåè°å½æ°
     * @param {*} entries è§å¯åç´ çä¿¡æ¯æ°ç»
     */
    observeHandler (entries) { 
         entries.forEach(entry => {
            if (entry.isIntersecting) {
                // åç´ è¿å¥è§å£ï¼å è½½å¾ç
                const img = entry.target;
                const dataSrc = img.getAttribute('data-src');
                if (dataSrc) {
                    // è®¾ç½®å¾çå è½½æååå¤±è´¥çå¤ç
                    img.onload = () => {
                        // å¾çå è½½æåï¼ç§»é¤äºä»¶çå¬
                        img.onload = null;
                        img.onerror = null;
                    };
                    
                    img.onerror = () => {
                        // å¾çå è½½å¤±è´¥ï¼ä½¿ç¨å¤±è´¥å ä½å¾
                        if (this.imgOptions.error) {
                            img.src = this.imgOptions.error;
                        }
                        img.onload = null;
                        img.onerror = null;
                    };
                    
                    img.src = dataSrc; // å è½½çå®å¾ç
                    img.removeAttribute('data-src');
                    // åæ­¢è§å¯è¯¥åç´ 
                    this.unobserve(img);
                }
            }
        });
    }
    
    /**
     * çå¬åç´ 
     * @param {Element} el DOMåç´ 
     */
    observe (el) {
        // ç¡®ä¿è§å¯å¨å®ä¾å­å¨
        this.initObserver();
        // å¦æåç´ å·²ç»ædata-srcå±æ§ä¸ä¸å¨è§å¯ä¸­ï¼åå¼å§è§å¯
        if (el.getAttribute('data-src') && !this.isObserving(el)) {
            this.observer.observe(el);
            el.dataset.isObserving = 'true';
        }
    }

    /**
     * æ£æ¥åç´ æ¯å¦æ­£å¨è¢«è§å¯
     * @param {Element} el DOMåç´ 
     * @returns {boolean} æ¯å¦æ­£å¨è¢«è§å¯
     */
    isObserving(el) {
        // æ£æ¥åç´ æ¯å¦æè¢«è§å¯çæ è®°
        return el.dataset.isObserving === 'true';
    }

    /**
     * åæ¶çå¬åç´ 
     * @param {Element} el DOMåç´ 
     */
    unobserve(el) {
        if (this.observer) {
            this.observer.unobserve(el);
            // ç§»é¤è§å¯æ è®°
            delete el.dataset.isObserving;
        }
    }

    /**
     * éæ¯è§å¯å¨
     */
    destroy() {
        if (this.observer) {
            this.observer.disconnect();
            this.observer = null;
        }
        this.element = [];
    }
}


if (window) {
    window.LazyLoad = LazyLoad;
}

//  ES Module
export default LazyLoad;
// commonJS ç¯å¢
if (typeof module !== 'undefined' && module.exports) {
  module.exports = LazyLoad;
}
